// Setup
//-------------------------------------

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
}

// Main Styles
//-------------------------------------

body {
  color: #1a202c;
  transition: color 0.2s ease-in;
}

#app {
  height: 100vh;
  width: 100%;
}

main {
  background-color: #f7fafc;
  height: 100%;
  width: 100%;
  padding: 20px;
  transition: background-color 0.2s ease-in;
}

#toggle {
  width: 50px;
  display: flex;
  padding: 5px;
  background-color: #1a202c;
  display: block;
  border-radius: 1000px;
  cursor: pointer;
  box-shadow: 0px 5px 20px -10px #000000;
  transition: background-color 0.2s ease-in;

  .toggle-inner {
    width: 20px;
    height: 15px;
    background-color: white;
    border-radius: 1000px;
    transition: margin-left 0.2s ease-in, background-color 0.2s ease-in;

    &.toggle-active {
      margin-left: 20px;
    }
  }
}

#container {
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  h1 {
    padding: 0;
    margin: 0 0 10px 0;
  }

  p {
    opacity: .8;
  }
}

// Dark Mode styling
//-------------------------------------

.dark-mode {
  color: white;

  main {
    background-color: #1a202c;
  }

  #toggle {
    background-color: white;

    .toggle-inner {
      background-color: #1a202c;
    }
  }
}

.btn__switch {
  display: flex;
  gap: 10px;

  .btn__scroll {
    border: none;
    background: none;
    display: block;
    margin: auto;
    cursor: pointer;
    transition: 0.1s;
  }
}